$main: white;

.app {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  min-height: 100%;
  padding: 80px;
  color: $main;
  font-size: 16px;
  font-family: 'Inconsolata', monospace;
  z-index: 2; }

.bg {
  position: fixed;
  top: 200px;
  left: 30%;
  width: 100%;
  height: 100%;
  background-image: url('https://i.imgur.com/UnB0Olf.jpg');
  background-image: radial-gradient(circle, rgba(0,0,0,0) -10%, rgba(0,0,0,1) 120%), url('https://i.imgur.com/UnB0Olf.jpg');
  background-size: cover;
  opacity: 0.7;
  z-index: 1; }


 :global {
  html, body {
    height: 100%; }

  body {
    min-height: 100%; }

  #root {
    width: 100%;
    height: 100%;
    min-height: 100%;
    overflow: hidden;
    background-color: black; } }
